<template>
  <div>
    <NarBar/>
    <div class="banner"></div>
    <div class="containers mr-tt">
      <a-row class="items" :gutter="24">
      <a-col :span="16">
        <div style="background: #fff" class="shadow card">
          <div class="head">
            <div style="width: 100%;">
              <img width="100%" height="200" :src="article.thumbnail">
            </div>
            <div class="h1 mr-t" style="text-align: center">
              {{article.title}}
            </div>
            <div class="mr-t" style="text-align: center">
              <span>
                <a-icon type="calendar" />
                  {{article.publish_time}}
                </span>
                <span class="mr-l">
                  <a-icon type="user" />
                    {{article.author}}
                  </span>
             </div>
          </div>
          <mavon-editor
            class="md"
            :boxShadow="false"
            :codeStyle="'tomorrow-night'"
            :value="article.contents"
            :subfield="prop.subfield"
            :defaultOpen="prop.defaultOpen"
            :toolbarsFlag="prop.toolbarsFlag"
            :editable="prop.editable"
            :scrollStyle="prop.scrollStyle"
            :boxShadowStyle="'none'"
          />
          <div style="margin-left: 22px; padding-bottom: 10px">
              <a-tag v-for="i in cates" color="pink" :key="i.id">{{i}}</a-tag>
          </div>
        </div>
        <div class="card mr-t" style="font-size: 20px; line-height: 1.5em; padding: 15px; color: black; background-color: rgba(255, 255, 255, 0.7); border-left: 3px solid skyblue; transition: opacity 0.3s ease-out 0s, transform 0.3s ease-out 0s; opacity: 1; transform-origin: center top;">
          <div class="card-content">
            <p style="font-size: 15px;">本文由 <a href="https://www.lwjppz.cn">{{article.author}}</a> 创作，如果您觉得本文不错，请随意赞赏<br>
                                        采用 <a href="https://creativecommons.org/licenses/by/4.0/" target="_blank" rel="external nofollow">知识共享署名4.0</a> 国际许可协议进行许可<br>本站文章除注明转载/出处外，均为本站原创或翻译，转载前请务必署名<br>原文链接：<a :href="'/article/' + article.id">{{article.title}}</a><br>最后更新于：{{article.publish_time}}
            </p>
          </div>
        </div>
        <a-card class="mr-t card">
          <div id="gitalk-container"></div>
        </a-card>
      </a-col>
        <a-col class="right-items" :span="8">
          <Profile/>
          <Category/>
          <Tag/>
        </a-col>
      </a-row>
    </div>
    <Footer/>
    <BackToTop/>
  </div>
</template>

<script>
  import NarBar from "./components/NarBar";
  import Profile from "./components/Profile";
  import Category from "./components/Category";
  import Tag from './components/Tag'
  import ArticleApi from "../../api/Articles";
  import { mavonEditor } from 'mavon-editor'
  import 'mavon-editor/dist/css/index.css'
  import Footer from "./components/Footer";
  import BackToTop from "./components/BackToTop";
  export default {
    name: "Article",
    components: {
      BackToTop,
      Footer,
      NarBar,
      Profile,
      Category,
      Tag,
      mavonEditor
    },
    data() {
      return {
        article: {},
        id: '',
        cates: [],
        // eslint-disable-next-line no-undef
        gitalk : new Gitalk({
          clientID: '1254cc140effd03a56ef',
          clientSecret: '0b4c6e268522d6c4a27a809b8705e95b877177ca',
          repo: 'gitalk',
          owner: 'Bursteretion',
          admin: ['Bursteretion'],
          id: location.pathname,      // Ensure uniqueness and length less than 50
          distractionFreeMode: false  // Facebook-like distraction free mode
        }),
      }
    },
    computed: {
      // 解析器配置
      prop () {
        let data = {
          subfield: false,// 单双栏模式
          defaultOpen: 'preview',//edit： 默认展示编辑区域 ， preview： 默认展示预览区域
          editable: false,	// 是否允许编辑
          toolbarsFlag: false,
          scrollStyle: true
        }
        return data
      }
    },
    mounted () {
      this.gitalk.render("gitalk-container");
    },
    created () {
      this.loadArticle()
    },
    methods: {
      async loadArticle() {
        this.id = this.$route.params.id
        ArticleApi.get(this.id).then(res => {
          if (res.code === 1000) {
            this.article = res.data.article
            this.cates = this.article.categories.split(',')
            window.document.title = this.article.title
          } else {
            this.$message.error('获取文章失败')
          }
        })
      }
    }
  }
</script>

<style lang="less" scoped>
  .banner {
    position: relative;
    width: 100%;
    height: 500px;
    background: url("https://cdn.jsdelivr.net/gh/Bursteretion/file-repo/imgs/20200407225122.jpg");
    background-size: 100% 100%;
  }
  .right-items > *:not(:first-child) {
    margin-top: 10px;
  }
  .containers {
    margin-bottom: 30px;
  }
  .card {
    padding: 20px;
  }
  @media screen and (max-width: 800px) {
    .containers {
      width: 95%;
      padding-right: 0 !important;
    }
    .items > * {
      padding-left: 12px;
      padding-right: 12px;
      width: 100%;
    }
    .items > *:last-child {
      margin-top: 10px;
    }
    .banner {
      height: 300px;
    }
  }
</style>